@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button.calc-button {
  font-family: "Audiowide", cursive;
  font-size: x-large;
  font-weight: bold;
  color: white;
  background-color: blue;
  margin: 2%;
  border-radius: 15%;
  min-height: 50%;
}

button.calc-button.size-1 {
  min-width: 15%;
}

button.calc-button.size-2 {
  min-width: 35%;
}

button.calc-button.size-3 {
  min-width: 53%;
}

.container {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #607d8b;
}

.border-cal {
  border: 5px solid red;
  width: 70vw;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 0, 0, 0.11);
}

.screen {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 5px solid purple;
  height: 20%;
  width: 80%;
  background-color: rgba(128, 0, 128, 0.192);
  position: relative;
}

.number-screen {
  font-family: "Audiowide", cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 5px solid black;
  color: black;
  font-size: x-large;
  font-weight: bold;
  text-align: center;
  width: 80%;
  height: 40%;
  background-color: white;
  vertical-align: middle;
}

.btns {
  border: 5px solid yellow;
  height: 50%;
  width: 80%;
  background-color: rgba(255, 255, 0, 0.26);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.btns .rows {
  margin-left: 20%;
}

.float {
  color: whitesmoke;
  position: absolute;
  right: 10%;
  top: 10%;
}

